import React, { Component } from 'react';
import PageLayout from '../elements/PageLayout';
import MyCards from '../elements/MyCards';
import WritingTemplate from './WritingTemplate';

let cardContents = [{
    link: '/novels/wolf',
    image: '/Image/wolf.jpg',
    header: '',
    description: '',
  },
  {
  link: '/novels/lastnight',
  image: '/Image/erranter.jpg',
  header: '',
  description: '',
  },
  {
  link: '/novels/lastnight',
  image: '/Image/snow.jpg',
  header: '',
  description: '',
  }];

let paragraphs = `










--------------------------




 


-----------------------------



 














 

 





 












 

 

 

 




 



:  

  





   



 








-------------------------







 

 

 

 



 



 



 







 





 









 

 















 



 

  





  



 

-------------------------



 

 

 

 




------------------ END --------

`

;

class LastNight extends Component {
  render() {
    return (

      <WritingTemplate
        img_cover="/Image/last_night_cover.jpg"
        title = ""
        content={cardContents}
      >
        {paragraphs.split('\n\n').map(paragraph => (
          <p key={paragraph}>{paragraph}</p>
        ))}
      </WritingTemplate>
    );
  }
}

export default LastNight;



