import React, { Component } from 'react';
import PageLayout from '../elements/PageLayout';
import MyCards from '../elements/MyCards';

class WritingPage extends Component {
  render() {
    return (
      <PageLayout>
        <div className="ts grid">
          <div className="row">
            <div className="one wide column" />
            <div className="forteen wide column">
                <div className="novel_block">
                  <div className="title dark_green">NOVELS</div>
                  <div className="ts stackable four cards">

                    <MyCards cardLink = "/novels/lastnight"
                    cardImage = "/Image/last_night.jpg"
                    cardHeader = ""
                    cardDescription = " "/>

                    <MyCards cardLink = "/novels/wolf"
                    cardImage = "/Image/wolf.jpg"
                    cardHeader = ""
                    cardDescription = ""/>

                    <MyCards cardLink = "/"
                    cardImage = "/Image/erranter.jpg"
                    cardHeader = ""
                    cardDescription = ""/>

                    <MyCards cardLink = "/"
                    cardImage = "/Image/snow.jpg"
                    cardHeader = ""
                    cardDescription = ""/>
                  </div>
                </div>
            </div>
            <div className="one wide column" />
          </div>
          <div className="row bottom_space">
            <div className="one wide column" />
            <div className="forteen wide column">
                <div className="novel_block">
                  <div className="title dark_green">DAILY POSTS</div>
                  <div className="ts stackable four cards">
                    <MyCards cardLink = "/" cardImage = "/Image/last_night.jpg" cardHeader = "" cardDescription = "HAHAHA"/>
                    <MyCards cardLink = "/" cardImage = "/Image/last_night.jpg" cardHeader = "" cardDescription = "HAHAHA"/>
                    <MyCards cardLink = "/" cardImage = "/Image/post.jpg" cardHeader = "" cardDescription = "HAHAHA"/>
                    <MyCards cardLink = "/" cardImage = "/Image/others.jpg" cardHeader = "" cardDescription = "HAHAHA"/>
                  </div>
                </div>
            </div>
            <div className="one wide column" />
          </div>
        </div>

      </PageLayout>
    )
  }
}

export default WritingPage;



